export default {
  async asyncData({
    $content,
    params
  }) {
    const articles = await $content('articles', params.slug)
      .where('article.category')
      .sortBy('createdAt', 'asc')
      .fetch()

    return {
      articles
    }
  },
  methods: {
  formatDate(date) {
    const options = { year: 'numeric', month: 'long', day: 'numeric' }
    return new Date(date).toLocaleDateString('en', options)
  }
},
}

